import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UsercreationComponent } from './usercreation.component';
import { MoBankdetailsComponent } from './mo-bankdetails/mo-bankdetails.component';
import { UserdetailsComponent } from './userdetails/userdetails.component';
import { AuthGuard } from '../auth/auth.guard';


const routes: Routes = [
    {
    path:'user-creation',component: UsercreationComponent,canActivate:[AuthGuard]
    },
    {
        path:'change-password',component: UserdetailsComponent,canActivate:[AuthGuard]
    },
    {path:'add-account',component: MoBankdetailsComponent,canActivate:[AuthGuard]},
    
    
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserCreation {}
